import React, { useState } from 'react';
import { Plus, Image, Calendar, Target, FileText, AlertCircle } from 'lucide-react';
import { useWeb3 } from '../hooks/useWeb3';
import toast from 'react-hot-toast';

const CreateProjectPage = () => {
  const { factoryContract, parseEther, isConnected } = useWeb3();
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    goal: '',
    duration: '',
    imageUrl: ''
  });
  const [loading, setLoading] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const validateForm = () => {
    if (!formData.title.trim()) {
      toast.error('');
      return false;
    }
    if (!formData.description.trim()) {
      toast.error('');
      return false;
    }
    if (!formData.goal || parseFloat(formData.goal) <= 0) {
      toast.error('');
      return false;
    }
    if (!formData.duration || parseInt(formData.duration) <= 0) {
      toast.error('');
      return false;
    }
    return true;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!isConnected) {
      toast.error('');
      return;
    }

    if (!validateForm()) {
      return;
    }

    setLoading(true);
    try {
      const goalInWei = parseEther(formData.goal);
      const durationInDays = parseInt(formData.duration);
      
      const tx = await factoryContract.createCrowdfunding(
        goalInWei,
        durationInDays,
        formData.title,
        formData.description,
        formData.imageUrl || ''
      );

      toast.loading('...', { id: 'create' });
      
      const receipt = await tx.wait();
      
      // 
      const event = receipt.logs.find(log => {
        try {
          return factoryContract.interface.parseLog(log).name === 'CrowdfundingCreated';
        } catch {
          return false;
        }
      });

      if (event) {
        const parsedEvent = factoryContract.interface.parseLog(event);
        const projectAddress = parsedEvent.args[1];
        
        toast.success(
          `: ${projectAddress.slice(0, 10)}...`,
          { id: 'create', duration: 5000 }
        );
      } else {
        toast.success('', { id: 'create' });
      }

      // 
      setFormData({
        title: '',
        description: '',
        goal: '',
        duration: '',
        imageUrl: ''
      });

    } catch (error) {
      console.error(':', error);
      
      let errorMessage = '';
      if (error.message.includes('user rejected')) {
        errorMessage = '';
      } else if (error.message.includes('insufficient funds')) {
        errorMessage = ' Gas ';
      }
      
      toast.error(errorMessage, { id: 'create' });
    } finally {
      setLoading(false);
    }
  };

  if (!isConnected) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full flex items-center justify-center mx-auto mb-4">
            <Plus className="w-8 h-8 text-white" />
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-2"></h2>
          <p className="text-gray-600 mb-6"></p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:px-8">
        {/*  */}
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2"></h1>
          <p className="text-gray-600"></p>
        </div>

        {/*  */}
        <div className="bg-white rounded-xl shadow-lg p-8">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                <FileText className="w-4 h-4 inline mr-2" />
                 *
              </label>
              <input
                type="text"
                name="title"
                value={formData.title}
                onChange={handleInputChange}
                placeholder=""
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent input-focus"
                maxLength={100}
              />
              <p className="text-xs text-gray-500 mt-1">
                {formData.title.length}/100 
              </p>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                <FileText className="w-4 h-4 inline mr-2" />
                 *
              </label>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                placeholder=""
                rows={6}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent input-focus resize-none"
                maxLength={1000}
              />
              <p className="text-xs text-gray-500 mt-1">
                {formData.description.length}/1000 
              </p>
            </div>

            {/*  */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  <Target className="w-4 h-4 inline mr-2" />
                   (ETH) *
                </label>
                <input
                  type="number"
                  step="0.001"
                  min="0.001"
                  name="goal"
                  value={formData.goal}
                  onChange={handleInputChange}
                  placeholder="1.0"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent input-focus"
                />
                <p className="text-xs text-gray-500 mt-1">
                  : 0.001 ETH
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  <Calendar className="w-4 h-4 inline mr-2" />
                   *
                </label>
                <input
                  type="number"
                  min="1"
                  max="365"
                  name="duration"
                  value={formData.duration}
                  onChange={handleInputChange}
                  placeholder="30"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent input-focus"
                />
                <p className="text-xs text-gray-500 mt-1">
                  1-365 
                </p>
              </div>
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                <Image className="w-4 h-4 inline mr-2" />
                 URL ()
              </label>
              <input
                type="url"
                name="imageUrl"
                value={formData.imageUrl}
                onChange={handleInputChange}
                placeholder="https://example.com/image.jpg"
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent input-focus"
              />
              <p className="text-xs text-gray-500 mt-1">
                 HTTPS 
              </p>
            </div>

            {/*  */}
            {formData.imageUrl && (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="border border-gray-300 rounded-lg p-4">
                  <img
                    src={formData.imageUrl}
                    alt=""
                    className="w-full h-48 object-cover rounded-lg"
                    onError={(e) => {
                      e.target.style.display = 'none';
                    }}
                  />
                </div>
              </div>
            )}

            {/*  */}
            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
              <div className="flex items-start">
                <AlertCircle className="w-5 h-5 text-blue-600 mr-2 mt-0.5" />
                <div className="text-sm text-blue-800">
                  <p className="font-medium mb-1"></p>
                  <ul className="text-xs space-y-1">
                    <li>  Gas  0.003-0.01 ETH</li>
                    <li> </li>
                    <li> </li>
                  </ul>
                </div>
              </div>
            </div>

            {/*  */}
            <div className="flex space-x-4">
              <button
                type="button"
                onClick={() => {
                  setFormData({
                    title: '',
                    description: '',
                    goal: '',
                    duration: '',
                    imageUrl: ''
                  });
                }}
                className="flex-1 py-3 px-4 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
              >
                
              </button>
              <button
                type="submit"
                disabled={loading}
                className="flex-1 py-3 px-4 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-lg hover:from-blue-600 hover:to-purple-700 transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center btn-gradient"
              >
                {loading ? (
                  <>
                    <div className="loading-spinner mr-2" />
                    ...
                  </>
                ) : (
                  <>
                    <Plus className="w-5 h-5 mr-2" />
                    
                  </>
                )}
              </button>
            </div>
          </form>
        </div>

        {/*  */}
        <div className="mt-8 bg-white rounded-xl shadow-lg p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4"></h3>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-sm text-gray-600">
            <div>
              <h4 className="font-medium text-gray-900 mb-2"></h4>
              <ul className="space-y-1">
                <li> </li>
                <li> </li>
                <li>  10-50 </li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium text-gray-900 mb-2"></h4>
              <ul className="space-y-1">
                <li> </li>
                <li> </li>
                <li> </li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium text-gray-900 mb-2"></h4>
              <ul className="space-y-1">
                <li> </li>
                <li>  Gas </li>
                <li> </li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium text-gray-900 mb-2"></h4>
              <ul className="space-y-1">
                <li> </li>
                <li>  7-90 </li>
                <li> </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CreateProjectPage; 